// IMPORTS
import { string } from 'prop-types';
import styled from 'styled-components';
import React from 'react';

// STYLES
const StyledInput = styled.input`
font-size: 20px;
color: #babaca;
padding: 16px;
`;

// COMPONENT
const Input = props => (
  <StyledInput {...props} />
);

// DOCUMENTATION
Input.propTypes = {
  placeholder: string, // texto dentro da parada
};

Input.defaultProps = {
  placeholder: 'CPF',
};

// EXPORT
export default Input;
